.header-content {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.headerbg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Позиціонуємо аватарку та ім'я поверх картинки */
.user-avatar {
  position: absolute;
  top: 24%;
  left: 7.5%;
  width: 15%;
  height: 50%;
  border-radius: 50%;
  border: 1px solid white;
  object-fit: cover;
  z-index: 2;
}

.nickname {
  font-family: 'GNF', sans-serif;
  position: absolute;
  top: 20%;
  left: 30%;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
  z-index: 2;
}
